<template>
  <div id="app">
    <vue-canvas-nest></vue-canvas-nest>
    <transition name="el-zoom-in-center"><keep-alive><router-view/></keep-alive></transition>
  </div>
</template>

<script>
  import vueCanvasNest from 'vue-canvas-nest'

  export default {
  name: 'App',
    components: { vueCanvasNest }
}
</script>

<style scoped>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 100%;
  height: 100%;
  /*background-color: blue;*/
  position:relative;
  top:0;
  left:0;
  margin: 0;
  padding: 0;
}
</style>



