<template>
  <div id="Sockshop">
    <!--<img src="../../images/sockshop.png" style="width: 100px;height: 100px; position: absolute; left: 10px;top: 0"/>-->
    <el-form
      v-model="test"
      ref="test"
      label-position="left"
      label-width="100px"
      style="text-align: left;margin-top: 60px;margin-left: 300px;margin-right: 300px;"
    >
      <el-form-item label="name">
        <el-input v-model="test.taskName" placeholder="please name the task"></el-input>
      </el-form-item>
      <el-form-item label="host">
        <el-input placeholder="required host" v-model="test.host"></el-input>
      </el-form-item>
      <el-form-item label="hatch rate">
        <el-input-number  v-model="test.hatchRate"></el-input-number>
      </el-form-item>
      <el-form-item label="run time(min)">
        <el-input-number v-model="test.runTime"></el-input-number>
      </el-form-item>
      <el-form-item label="users">
        <el-input-number
          v-model="test.clients"
          placeholder="please input the number of users"
        ></el-input-number>
      </el-form-item>
      <el-form-item label="scene">
      <el-select v-model="test.scenarioId">
      <el-option v-for="(item,index) in scene" :key="index" :label="item.name" :value="item.value"></el-option>
      </el-select>
      </el-form-item>
      <el-form-item><el-button type="primary" @click="start" round>Start Testing!</el-button></el-form-item>
    </el-form>
    <el-steps :active="active" finish-status="success" v-if="show" simple process-status="process">
      <el-step title="deal data"></el-step>
      <el-step title="prepare test"></el-step>
      <el-step title="start test"></el-step>
    </el-steps>
    <el-progress type="circle" :percentage="percent" status="success" v-if="show"></el-progress>
    <br/>
    <br/>
    <br/>
    <br/>
    <br/>
    <br/>
    <br/>
    <br/>
  </div>
</template>

<script>
  export default {
    name: "Sockshop",
    data() {
      return {
        test: {
          host: "",
          clients: 0,
          recordId: 1,
          taskName:"",
          hatchRate:0,
          runTime:1,
          scenarioId:0
        },
        scene:[{name:"homepage",value:0},{name:"register",value:1},{name:"browse",value:2},{name:"add to cart",value:3},{name:"payment",value:4}],
        show:false,
        active:0,
        percent:0
      };
    },

    methods: {
      start() {
        // 开始测试,向后端请求
        this.show=true;
        let _self=this;
        let time=window.setInterval(()=>{
          _self.active++;
          _self.percent=_self.percent+33;
          if (_self.active>=3){
            clearInterval(time);
            _self.show=false;
            _self.active=0;
            _self.percent=0;
            _self.axios.post('/workload',_self.qs.stringify(_self.test)).then(function (res) {
              _self.test.recordId=res.data;
              _self.$router.push({
                path: "result",
                query: { start:(_self.test.runTime*60),recordId: _self.test.recordId}
              });
            }).catch(function () {
              _self.$message("Network error");
            })
          }
        },4000);

      }
    }
  };
</script>

<style scoped>
  #Sockshop {
    font-family: "Avenir", Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    /*position: absolute;*/
    height: 100%;
    width: 100%;
    /*top: 110px;*/
    /*left: 200px;*/
  }
</style>



