<template>
  <div id="Logup">
    <el-col :span="12">
      <img src="../../images/logo.png" style="margin: 250px 120px 250px 100px"/>
    </el-col>
    <el-col :span="12">
      <el-form label-position="left" label-width="0px" class="login-container">
        <el-form-item label="account"
        ><el-input type="text" placeholder="Email" v-model="account"
        /></el-form-item>
        <el-button @click="send" type="primary" style="width: 100%; font-size: large">Send verification</el-button>
        <el-form-item label="verification">
          <el-input v-model="verification" placeholder="please input the verification you have received"></el-input>
        </el-form-item>
        <!--<el-button type="primary" @click="sendVerification">发送验证码</el-button>-->
        <!--<input type="text" placeholder="verification" v-model="veri" :class="'log-input' + (veri === '' ? ' log-input-empty' : '')"/>-->
        <el-form-item label="username">
          <el-input type="text" placeholder="Username" v-model="username" />
        </el-form-item>
        <el-form-item label="password">
          <el-input type="password" placeholder="Password" v-model="password"
          /></el-form-item>
        <el-button @click="logup" type="primary" style="width: 100%; font-size: large">Sign up</el-button>
      </el-form>
      <Loading v-if="isLoging" marginTop="-30%"></Loading>
    </el-col>
  </div>
</template>

<script>
import Loading from "../components/Loading.vue";
export default {
  name: "Logup",
  data() {
    return {
      isLoging: false,
      account: "",
      password: "",
      username: "",
      verification:"",
      veri:""
    };
  },
  components: {
    Loading
  },
  methods: {
    // 登录逻辑
    logup() {
      let _self = this;
      if (this.account === "" || this.password === "" || this.username === "") {
        _self.$alert("invalid account and password","fail",{confirmButtonText:"OK",type:"error"});
      } else {
        if (_self.veri.replace(/(^\s*)|(\s*$)/g, "")!==_self.verification.replace(/(^\s*)|(\s*$)/g, "")){
          _self.$alert("please input correct verification","incorrect verification",{confirmButtonText:"OK",type:"error"})
        }
        else {
          _self.axios.put(
              "/user/logup", _self.qs.stringify({
                account: _self.account,
                password: _self.password,
                username: _self.username
              }))
            .then(function (res) {
              if (res.data["state"] === 1) {
                _self.$alert("Click the OK to visit the homepage", "success", {
                  confirmButtonText: "OK",
                  type: "success",
                  callback: () => {
                    window.localStorage.setItem(
                      "user",
                      JSON.stringify(res.data.data)
                    );
                    _self.$router.push("/home");
                  }
                });
              } else {
                _self.$alert("the account has been registered", "fail", {
                  confirmButtonText: "OK",
                  type: "error"
                });
              }
            })
            .catch(function () {
              _self.$alert("network error", "fail", {confirmButtonText: "OK", type: "error"});
            });
        }
      }
    },
    send(){
      let _self=this;
      let reg=/^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
      if (!reg.test(_self.account)){
        _self.$alert("please input an email address","incorrect account",{confirmButtonText:"OK"})
      }else{
        _self.axios.get("/user/verification?account="+_self.account).then(function (res) {
          if (res.data.state===1){
            _self.veri=res.data.data
          }
          else{
            _self.$alert("please check your account", "fail", {confirmButtonText: "OK", type: "error"});
          }
        })
      }
    }
  }
};
</script>

<style lang="scss" scoped>
  .login-container {
    /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin:50px auto;
    width: 350px;
    padding: 20px 35px 35px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #000000;
    .title {
      margin: 0px auto 20px auto;
      text-align: center;
      color: #314758;
    }
    .remember {
      margin: 0px 0px 35px 0px;
    }
  }
</style>



