<template>
  <div id="Login">
    <el-col :span="12">
      <img src="../../images/logo.png" style="margin: 250px 120px 250px 100px"/>
    </el-col>
    <el-col :span="12">
      <el-form label-position="left" label-width="0px" class="login-container">
        <el-form-item label="account">
          <el-input placeholder="Email" v-model="account"></el-input>
        </el-form-item>
        <el-form-item label="password">
          <el-input type="password" placeholder="Password" v-model="password"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button @click="login" type="primary" style="width: 100%; font-size: large">Sign in</el-button>
        </el-form-item>
        <el-form-item>
          <p style="font-size: medium">If you have no account,please <router-link to="/logup">register</router-link></p>
          <p style="font-size: medium;">If you forget your password,you can <router-link to="/forget">retrieve it</router-link></p>
        </el-form-item>
      </el-form>
      <Loading v-if="isLoging" marginTop="-30%"></Loading>
    </el-col>
  </div>
</template>

<script>
  import Loading from "../components/Loading.vue";
export default {
  name: "Login",
  data() {
    return {
      isLoging: false,
      account: "",
      password: "",
    };
  },
  components: {
    Loading
  },
  methods: {
    // 登录逻辑
    login() {
      if (this.account !== "" && this.password !== "") {
         let _self=this;
        _self.axios.post('/user/login',_self.qs.stringify({account:_self.account,password:_self.password})).then(function (res) {
          if (res.data["state"]===1){
            window.localStorage.setItem("user",JSON.stringify(res.data.data));
            _self.$router.push("/home");
          }
          else{
            _self.$alert(res.data.msg,"Fail",{confirmButtonText:"OK",type:"error"})
          }
        }).catch(function () {
          _self.$alert("network error","Fail",{confirmButtonText:"OK",type:"error"})
        });
      }
    }
  }
};
</script>

<style lang="scss" scoped>
  .login-container {
    /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin-top: 100px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 80px;
    width: 350px;
    padding: 20px 35px 20px 35px;
    background: rgba(255, 255, 255, 0.67);
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #000000;
    .title {
      margin: 0px auto 20px auto;
      text-align: center;
      color: #314758;
    }
    .remember {
      margin: 0px 0px 35px 0px;
    }
  }

</style>



