<template>
  <div id="User">
      <span><h2>Your information</h2></span>
      <el-form class="login-container" style="margin-top: 20px; margin-bottom: 0">
      <el-form-item label="account">
        <el-input v-model="user['account']" disabled></el-input>
      </el-form-item>
        <el-form-item label="username">
          <el-input v-model="user['username']"></el-input>
        </el-form-item>
        <el-form-item label="company">
          <el-input v-model="user['company']"></el-input>
        </el-form-item>
      <el-form-item label="company_size">
        <el-input-number v-model="user['company_size']"></el-input-number>
      </el-form-item>
      <el-form-item label="job">
        <el-input v-model="user['job']"></el-input>
      </el-form-item>
        <el-button type="primary" @click="save" style="width: 100%;" >Save</el-button>
      </el-form>
  </div>
</template>

<script>
export default {
  name: "User",
  data() {
    return {
      user: {}
    };
  },
  mounted(){
    this.user=JSON.parse(window.localStorage.getItem("user"));
  },
  methods: {
    save() {
      let _self=this;
      _self.axios.patch("/user/update",_self.qs.stringify({username:_self.user["username"],job:_self.user["job"],company:_self.user["company"],company_size:_self.user["company_size"]})).then(function (res) {
        if (res.data["state"]===1){
          window.localStorage.setItem("user",JSON.stringify(res.data.data));
          _self.$alert("Change successfully","Success!",{confirmButtonText:"OK",type:"success"});
        }
        else{
          _self.$alert(res.data["msg"],"Fail",{confirmButtonText:"OK",type:"error"});
        }
      }).catch(function () {
        _self.$alert("network error","fail",{confirmButtonText:"OK",type:"error"})
      })
    }
  }
};
</script>

<style scoped>
  .login-container {
    /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin-top: 100px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 80px;
    width: 350px;
    padding: 20px 35px 20px 35px;
    background: rgba(255, 255, 255, 0.67);
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #000000;
  }
</style>



