<template>
  <div id="ModifyPassword">
      <h2>Change your password</h2>
      <el-form class="login-container">
        <el-form-item label="verification"
          ><el-input
            v-model="veri"
            placeholder="please input the verification you have received"
          ></el-input><el-button @click="send" style="border: none">Send verification</el-button>
        </el-form-item>
        <el-form-item label="password"
          ><el-input
            v-model="password" type="password"
            placeholder="please input new password"
          ></el-input
        ></el-form-item>
        <el-button type="primary" style="width: 100%"  @click="save">Save</el-button>
      </el-form>
  </div>
</template>

<script>
export default {
  name: "ModofyPassword",
  data() {
    return {
      veri: "",
      password: "",
      verification: "",
      account: "123@example.com"
    };
  },
  mounted() {
    this.account = this.$route.params.account;
    this.verification = "";
  },
  methods: {
    save() {
      if (this.verification.replace(/(^\s*)|(\s*$)/g, "") !== this.veri.replace(/(^\s*)|(\s*$)/g, "")) {
        this.$message("incorrect verification");
      } else {
        let _self=this;
        _self.axios.patch("/user/update/password",_self.qs.stringify({password:_self.password,account:_self.account})).then(function (res) {
          if (res.data["state"]===1){w
            _self.$message({type:"success",message:"success"});
          }
          else{
            _self.$message({type:"error",message:res.data["msg"]})
          }
        }).catch(function () {
          _self.$message({type:"error",message:"network error"})
        })
      }
    },
    send(){
      let _self=this;
      _self.axios.get("/user/verification?account="+_self.account).then(function (res) {
        if (res.data.state===1){
          _self.verification=res.data["data"];
        }
        else{
          _self.$message({type:"warning",message:res.data["msg"]})
        }
      }).catch(function () {
        _self.$message({type:"error",message:"network error"})
      })
    }
  }
};
</script>

<style scoped>
#ModifyPassword {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  position: relative;
  height: 100%;
  width: 100%;
}

.login-container {
  /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
  -webkit-border-radius: 5px;
  border-radius: 5px;
  -moz-border-radius: 5px;
  background-clip: padding-box;
  margin-top: 100px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 80px;
  width: 350px;
  padding: 20px 35px 20px 35px;
  background: rgba(255, 255, 255, 0.67);
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #000000;
}
</style>



