<template>
  <div id="Home">
    <el-container>
      <el-header style="height: 70px">
        <el-row>
          <el-col :span="4">
            <img src="../../images/logo.png" style="height: 55px;width: 200px"/>
          </el-col>

        <!--<div-->
          <!--style="text-align: center;line-height: 70px;font-size: 28px;height: 70px"-->
        <!--&gt;-->
          <el-col :span="7" :offset="5" style="padding: 20px">
          <!--<span style="font-size: x-large">-->
            <!--Loading Test-->
          <!--</span>-->
          </el-col>
        <!--</div>-->
        <!--<div-->
          <!--style="text-align: right;height: 10px;line-height: 10px"-->
        <!--&gt;-->
          <el-col :span="4" :offset="4" style="padding: 30px; color: white">
          <el-dropdown>
            <span class="el-dropdown-link" style="color: white; font-size: large">
            {{ username }}<i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item><span @click="jumpToInfo">User profile</span></el-dropdown-item>
              <el-dropdown-item><span @click="Modify">Change password</span></el-dropdown-item>
              <el-dropdown-item><span @click="Signout">Sign out</span></el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        <!--</div>-->
          </el-col>
        </el-row>
      </el-header>
      <el-main>
        <el-container>
          <el-aside width="200px;" style="bottom: 0">
            <el-menu :default-active="operation" class="el-menu-vertical-demo" text-color="#fff" background-color="#545c64" active-text-color="#ffd04b" @select="select">
              <el-submenu index="1" @click="newTest">
                <template slot="title"><i class="el-icon-circle-plus"></i><span>New test</span></template>
                <el-menu-item-group>
                  <el-menu-item @click.native="newTest(1)" index="1-1">Run Default</el-menu-item>
                  <el-menu-item @click="newTest(2)" index="1-2">Sock Shop Demo</el-menu-item>
                  <el-menu-item @click.native="newTest(0)" index="1-3">Define New</el-menu-item>
                </el-menu-item-group>
              </el-submenu>
              <el-submenu index="2">
                <template slot="title"><i class="el-icon-tickets"></i><span>My Records</span></template>
                <el-submenu v-for="(item, index) in history" :key="index" :index="'2-' + (index + 1)">
                  <template slot="title"><div @click="result(item)">
                    <!--<i class="material-icons">-->
                      <!--{{icon[item['scenario_id']]}}-->
                    <!--</i>-->
                    {{item["task_name"]}}</div></template>
                  <el-menu-item :index="'2-' + (index + 1)+'-1'" @click="reload">Check for details</el-menu-item>
                  <el-menu-item :index="'2-' + (index + 1)+'-2'" @click="deleteItem(index,item)">Delete</el-menu-item>
                </el-submenu>
              </el-submenu>
            </el-menu>
          </el-aside>
          <el-main>
            <transition name="el-zoom-in-center"><keep-alive><router-view :key="$route.fullPath"/></keep-alive
            ></transition>
          </el-main>
        </el-container>
      </el-main>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      account: "",
      username: "",
      operation: "1-1",
      history: [{ task_name: "", report_id: "" }],
      // icon:{'0':"schedule",'1':"person_plus",'2':"language",'3':"add_shopping_cart",'4':"payment"}
    };
  },
  mounted() {
    let user = JSON.parse(window.localStorage.getItem("user"));
    this.account = user["account"];
    this.username = user["username"];
    let _self = this;
    _self.axios.get("/history")
      .then(function(res) {
        _self.history = res.data;
      })
      .catch(function() {
        _self.$alert("Network error","Request failed", {
          confirmButtonText: "OK",
          type: "error"
        });
      });
    let operation=JSON.parse(window.localStorage.getItem("operation"));
    if (operation===null){
      this.operation="1-1";
    }
    else{
      this.operation=operation;
    }
  },
  methods: {
    deleteItem(index,item){
      let _self=this;
      debugger;
      _self.axios.delete("/delete?reportId="+item["report_id"]).then(function () {
        _self.history.splice(index,1)
      });
    },
    select(index,indexPath){
      this.operation=index;
    },
    newTest(select) {
      if (select === 1) {
        this.$router.push({ path: "defaultScene" });
      } else if (select===0) {
        this.$router.push({ path: "otherScene" });
      }else {
        this.$router.push({path: "sockshop"});
      }
    },
    reload(){
      window.localStorage.setItem("operation",JSON.stringify(this.operation));
      debugger;
      window.location.reload()
    },
    jumpToInfo() {
      this.$router.push("/user");
    },
    result(item) {
      debugger;
      this.$router.push({
        path: "result",
        query: { recordId: item["report_id"].toString(), start: 2 }
      });
    },
    Modify() {
      this.$router.push({
        name: "ModifyPassword",
        params: { account: this.account.toString() }
      });
    },
    Signout() {
      window.localStorage.removeItem("user");
      window.localStorage.removeItem("operation");
      this.axios.get("/user/out");
      this.$router.push({ path: "/" });
    }
  }
};
</script>

<style scoped>
#Home {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  position: relative;
  height: 100%;
  width: 100%;
}
.el-aside,.el-header,.el-main{
  margin: 0;
  padding: 0;
}
.el-aside {
  background-color: #545c64;
  text-align: left;
}
.el-header {
  background-color: #3a5c93;
  color: white;
  position: relative;
}
.div {
  margin: 0;
  padding: 0;
}
</style>



